import React from 'react';
import {inject, observer} from 'mobx-react';
import {Breadcrumb,Form, message} from 'antd';
import url from 'url';
import emitter from "@/utils/events";
import * as  mobx from 'mobx';

import ItemInfoNew from "@/components/worktile/ItemInfoNew";
import WortileCommonDetail from "@/components/worktile/WortileCommonDetail";
import ProjectDesignDetail from "@/components/worktile/ProjectDesignDetail";
import ProgressList from '@/components/worktile/ProgressList';
import * as utils from "@/utils/utils";

let flatData = []; // 

@Form.create()
@inject('WorktileDetailStore')
@observer
export default class WorkDetail extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.WorktileDetailStore;
		this.state = {count: 2};
        mobx.autorun(() => {
            this.store.commonData = this.store.getcommonData;
            this.store.proDesign = this.store.getproDesign;
        });
    }

	componentWillMount() {

	}

	componentDidMount() {
        let search = this.props.location.search;
        const {projectid, id, pid, stageid} = url.parse(search, true).query;
        this.store.projectId = projectid;
        // 
        this.store.getUserInfoListByRoleCode();
        // sts
        this.store.getSts();
        //
        this.store.getMyTaskCount();
		if(projectid!=null){
			this.store.getTree(projectid).then((mes) => {
			    this.store.treeData = Object.assign({}, mes); // 
				// 
				this.store.itemObj=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="")[0]);
				// 
				let proDesign=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="")[0]);
				// 
				this.store.commonData=Object.assign([], mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName!==""&&item.stageName!==""));
                flatData=[]
				// 
                this.treeData2FlatData(mobx.toJS(this.store.itemObj));
                this.store.projectName = flatData.filter((item)=>{ return item.treeType=="projectinfo"})[0]["name"];
                this.store.state = flatData.filter((item)=>{ return item.treeType=="projectinfo"})[0]["state"];
                this.store.closeReason = flatData.filter((item)=>{ return item.treeType=="projectinfo"})[0]["closeReason"];
                if(pid!==undefined&&id!==undefined){
                    emitter.emit("handletaskid",  {"pid":pid, "id":id});
                }else if (id!==undefined) {  // 
                    this.scrollToAnchor(id);
                }else if(stageid!==undefined){
                    this.scrollToAnchor(`items${parseInt(stageid)-1}`);
                }
                // 
                let commonData = JSON.parse(JSON.stringify(this.store.commonData));
                let obj = {};
                commonData.map((v)=>{
                    obj[v.id] = true;
                });
                this.store.comStates=Object.assign({}, obj);

                // statgeId
				this.store.proDesign = proDesign;
				this.store.oldStageId = proDesign.id;
				this.store.currentStageId = proDesign.id;
				// stage
				this.store.getStageList();
				// 
				this.store.getStageTree();
			});
		}
	}

    // 
    convert2flatData = (source)=>{
        source.forEach(el=>{
            let {children, ...item} = el;
            flatData.push(item);
            el.children && el.children.length>0 ? this.convert2flatData(el.children) : ""        // 
        })
    }

    treeData2FlatData = (source) => {
        let {children, ...item} = source;
        flatData.push(item);
        this.convert2flatData(children);
    }

    unique=(data)=>{
        let result = {};
        let finalResult=[];
        for(let i=0;i<data.length;i++){
            result[data[i].id]=data[i];
        }
        for(let item in result){
            finalResult.push(result[item])
        }
        return finalResult;
    }

	// 
	saveProject = (params) => {
	    this.store.saveProject(params)
		.then((res) => {
			if (res.id !== null) {
				message.success("")
                this.child.onChangeVis();
                this.child.callback("1");
				this.store.projectId = res.id;
                this.store.closeReason = res.closeReason;
                this.store.projectName = res.name;
                this.store.state = res.state;
                // tree
                let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                this.store.treeData = utils.updateTreeData(treeData, "update", res);
                // 
                this.store.getStageTree();
			} else {
				message.error("")
			}
		});
	}

	// updateTree
    updateTree = () => {
	    const projectId = this.store.projectId;
        this.store.getTree(projectId).then((mes) => {
            //  
            this.store.itemObj=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="")[0]);
            //  
            let proDesign=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="")[0]);
            //
            let commonData=Object.assign([], mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName!==""&&item.stageName!==""));
            flatData=[]
            // 
            this.treeData2FlatData(mobx.toJS(this.store.itemObj));
            this.store.projectName = flatData.filter((item)=>{ return item.treeType=="projectinfo"})[0]["name"];
            this.store.state = flatData.filter((item)=>{ return item.treeType=="projectinfo"})[0]["state"];
            this.store.closeReason = flatData.filter((item)=>{ return item.treeType=="projectinfo"})[0]["closeReason"];
            // 
            // Object.keys(editingData).map((item)=>{
            //     let newData = editingData[item];
            //     if (newData["id"]=="") {
            //         const transPid = (source)=>{
            //             source.forEach(el=>{
            //                 if (el.id==newData["pid"]) {
            //                     el.children.push(Object.assign({}, newData));
            //                 }
            //                 el.children && el.children.length>0 ? transPid(el.children) : ""        // 
            //             })
            //         }
            //         transPid(commonData);
            //     } else {
            //         const trans = (source)=>{
            //              source.forEach((el,elIndex)=>{
            //                 if (el.id==newData["id"]) {
            //                     el = Object.assign({}, el, newData);
            //                     source[elIndex] = el;
            //                 }
            //                 el.children && el.children.length>0 ? trans(el.children) : ""        // 
            //             })
            //             return source;
            //         }
            //         const dataSource = trans(commonData);
            //         commonData = dataSource;
            //
            //         const designSource = trans(proDesign.children);
            //         proDesign = Object.assign(proDesign, {children: designSource});
            //     }
            // });
            this.store.commonData = commonData;
            this.store.proDesign = proDesign;
        });
    }

	// 
	saveNodes = (params) => {
		this.store.saveNodes(params).then((res)=>{
           if (res) {
           	message.success("")
            // tree
            let treeData = JSON.parse(JSON.stringify(this.store.treeData));
            this.store.treeData = utils.updateTreeData(treeData, "update", res);
            // 
            this.store.getStageTree();
            //
            this.store.getMyTaskCount();
           } else {
	           message.error("")
           }
        });
    }

    // 
	saveStage  = (params) => {
		if(params&& params.projectId!=null)this.store.projectId = params['projectId']
		this.store.saveStage(params).then((res)=>{
			if (res) {
				message.success("")
                // 
                this.store.getStageTree();
			} else {
				message.error("")
			}
		});
	}

	updateState = (params) => {
        this.store.proDesign.masters = Object.assign([], params);
	}

	// 
	updateCount = (type, count) => {
		this.setState({
			[type]: count
		});
	}

	// 
	scrollToAnchor = (anchorName) => {
		if (anchorName) {
            try {
                let anchorElement = document.getElementById(anchorName);
                if (anchorElement) {
                    anchorElement.scrollIntoView({block: 'center'});
                }
            } catch(err) {
                console.log("scrollToAnchorERR=>"+err);
                // run plan B!
            }
		}
	}

    onRef = (ref) => {
        this.child = ref
    }

    // 
    saveAttachment = (params) => {
        this.store.saveAttachments(params)
            .then((res)=>{
                if (res) {
                    message.success("")
                    // tree
                    let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                    this.store.treeData = utils.updateTreeData(treeData, "updateChildren", res);
                } else {
                    message.error("")
                }
            });
    }

    // 
    delNode = (param) => {
        this.store.delNode(param)
            .then((res)=>{
                if (res) {
                    message.success("")
                } else {
                    message.error("")
                }
            });
    }

	render() {
        const {ossData, comStates, listData, itemObj, commonData, proDesign,  projectId, projectName} = this.store;
		const {count} = this.state;
		let isShow = projectId==null||projectId==""?true:false;
		return (
			  <div>
                  <div className="con_bottom" style={{backgroundColor: '#FFF'}}>
                      <Breadcrumb separator=">" style={{margin: '0'}}>
                          <Breadcrumb.Item href={"#/bpm/progresslist"}></Breadcrumb.Item>
                          <Breadcrumb.Item>{projectName||""}</Breadcrumb.Item>
                      </Breadcrumb>
                  </div>
				{/**/}
                 <ProgressList
                      listData={mobx.toJS(listData)}
                      store={this.store}
                      scrollToAnchor={this.scrollToAnchor}
                      updateTree={this.updateTree}
                      isShow={isShow}
                 />
				{/**/}
				  <ItemInfoNew
					  itemObj={mobx.toJS(itemObj)}
					  saveProject={this.saveProject}
					  projectId={projectId}
					  projectType={1}
					  onRef={this.onRef}
					  state={this.store.state}
					  saveAttachment={this.saveAttachment}
					  delNode={this.delNode}
					  ossData={mobx.toJS(ossData)}
					  hidden={true}
					  count={count}
					  updateCount={this.updateCount}
				  />
                {/**/}
				<ProjectDesignDetail
					proDesign={mobx.toJS(proDesign)}
					saveNodes={this.saveNodes}
					saveStage={this.saveStage}
					updateState={this.updateState}
                    saveAttachment={this.saveAttachment}
                    delNode={this.delNode}
                    store={this.store}
                    oss={mobx.toJS(ossData)}
                />
				{/**/}
				{mobx.toJS(commonData).length>0&&mobx.toJS(commonData).map((item, index)=>{
					return (<WortileCommonDetail
						  key={'card_'+index}
						  id={item.id}
                          rowIndex={"items"+(index+2)}
                          store={this.store}
                          treeData={item}
						  saveNodes={this.saveNodes}
                          saveAttachment={this.saveAttachment}
                          delNode={this.delNode}
                          disabled={mobx.toJS(comStates)[item.id]}
                          state={this.store.state}
                          oss={mobx.toJS(ossData)}
                    />)
				})}
			</div>
		);
	}
}







